import React, { useContext } from 'react'
import "./pageHeader.css"
import WhatsAppIcon from '@material-ui/icons/WhatsApp';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ExpandLessIcon from '@material-ui/icons/ExpandLess';
import { Context } from '../../Context';


function PageHeader({ title, catchLine}) {

    const [expandMenu, setExpandMenu, toogleExpand] = useContext(Context)

    return (
        <div className="header-wrapper">
            <h1>{title}</h1>
            <div className="catch-line-container">
                <p>{catchLine}</p>
                <a href="https://api.whatsapp.com/send?phone=393311654417" aria-label="whatsapp" target="_blank" rel="noreferrer"><WhatsAppIcon style={{ fontSize: 35 }} /></a>
            </div>
            <button onClick={toogleExpand} className="btn-expand" aria-label="expand menu">
                {expandMenu ? "Espandi" : "Restringi"}
                {expandMenu ? <ExpandMoreIcon /> : <ExpandLessIcon /> }
            </button>
        </div>
    )
}

export default PageHeader
